	module.exports = {"data":{"wordpressPost":{"title":"More bed sharing, baldness and vitamin C: Most read articles in June","content":"<p>&nbsp;</p>\n<p>The most-read article in June was once again Carpenter <em>et al</em>&#8216;s article on bed sharing with non-smoking parents and the risks of SIDS, which received widespread <a href=\"http://www.bbc.co.uk/news/health-22594587\">press coverage</a>. Another popular title was Yamada <em>et al</em>&#8216;s paper on the associations between male pattern baldness and coronary heart disease. Other newly-published papers in the top ten include Hemilä <em>et al</em>&#8216;s study on the way in which vitamin C may help alleviate exercise induced bronchoconstriction and Ford <em>et al</em>&#8216;s study of current treatments in diabetic macular ordema.</p>\n<table width=\"483\" border=\"1\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Carpenter <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/5/e002299\">Bed sharing when parents do not smoke: is there a risk of SIDS? An individual level analysis of five major case–control studies</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Humphriss <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/6/e002718\">Prenatal alcohol exposure and childhood balance ability: findings from a UK birth cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Ford <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/3/e002269\">Current treatments in diabetic macular oedema: systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Katzmarzyk <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/2/4/e000828\">Sedentary behaviour and life expectancy in the USA: a cause-deleted life table analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Yamada <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/4/e002537\">Male pattern baldness and its association with coronary heart disease: a meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Gotts<em> et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/6/e002999\">Are there sleep-specific phenotypes in patients with chronic fatigue syndrome? A cross-sectional polysomnography analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Morgenstern <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/6/e002907\">From never to daily smoking in 30 months: the predictive value of tobacco and non-tobacco advertising exposure</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Schmidt <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/4/e002698\">Obesity in young men, and individual and combined risks of type 2 diabetes, cardiovascular morbidity and death before 55 years of age: a Danish 33-year follow-up study</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Doyle <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/1/e001570\">A systematic review of evidence on the links between patient experience and clinical safety and effectiveness</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Hemilä <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/6/e002416\">Vitamin C may alleviate exercise-induced bronchoconstriction: a meta-analysis</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby for BMJ blog sites"}}},"pathContext":{"id":"0e537114-14cd-5085-8cb5-5541062a25ed"}}

